@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
.mb-8 {
  margin-bottom: 8rem !important;
} /*

0 - 600px:          Phone
600px - 900px       Table Portrait
900px - 1200px      Table Landscape
[1200px - 1800px]   Desktop Normal Styles
1800px +            Big Desktop

1em = 16px

ORDER: Base + Typography > Generar Layout + Grid > Page Layout > Components

*/
html {
  font-size: 62.5%;
}
html.sr .load-hidden {
  visibility: hidden;
}

body {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  scroll-behavior: smooth;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #d2d2d2;
}

::-webkit-scrollbar-thumb:hover {
  background: #02aab0;
}

h1 {
  font-weight: 700;
}

p,
a {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

a,
a:link,
a:hover,
a:visited,
a:active {
  text-decoration: none;
}

a:hover {
  transition: all 0.3s ease-in-out;
}

.section-title {
  margin: 0px;
  margin-bottom: 4.5rem;
  font-size: 4rem;
  font-weight: bold;
  text-transform: uppercase;
}
@media (max-width: 37.5em) {
  .section-title {
    font-size: 2.8rem;
  }
}

.dark-blue-text {
  color: #272341 !important;
}

.text-color-main {
  color: #00cdac;
  -webkit-text-fill-color: #00cdac;
}
@supports (-webkit-background-clip: text) or (background-clip: text) {
  .text-color-main {
    background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: transparent;
  }
}
@media (max-width: 37.5em) {
  .text-color-main {
    background-image: none;
    -webkit-text-fill-color: #00cdac;
  }
}
.text-color-main:hover {
  transform: translateX(2px);
}

/* Call to Action Button */
.cta-btn {
  display: inline-block;
  position: relative;
  padding: 0.8rem 1.6rem;
  font-weight: bold;
  line-height: 1;
  z-index: 1;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.6s;
}
.cta-btn::after {
  content: "";
  display: block;
  position: absolute;
  width: 0px;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.3s;
}

/* Hero Style */
.cta-btn--hero {
  color: #00cdac;
  -webkit-text-fill-color: #00cdac;
  border-width: 2px;
  border-style: solid;
  -moz-border-image: -moz-linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
  -webkit-border-image: -webkit-linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
  border-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
  -webkit-border-image-slice: 1;
  border-image-slice: 1;
}
@supports (-webkit-background-clip: text) or (background-clip: text) {
  .cta-btn--hero {
    background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: transparent;
  }
}
@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .cta-btn--hero {
    color: #00cdac !important;
  }
  .cta-btn--hero:hover {
    color: #fff !important;
  }
}
@media (max-width: 37.5em) {
  .cta-btn--hero {
    background-image: none;
    border: 2px solid #00cdac;
    -webkit-text-fill-color: #00cdac;
  }
}
.cta-btn--hero::after {
  background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
}
@media (max-width: 37.5em) {
  .cta-btn--hero::after {
    background-image: none;
  }
}
.cta-btn--hero:hover {
  -webkit-text-fill-color: #fff;
  text-decoration: none;
}
@media (max-width: 37.5em) {
  .cta-btn--hero:hover {
    -webkit-text-fill-color: #00cdac;
  }
}
.cta-btn--hero:hover::after {
  width: 100%;
}

/* Resume Style */
.cta-btn--resume {
  color: #fff;
  border: 2px solid #fff;
}
.cta-btn--resume::after {
  background: #fff;
}
.cta-btn--resume:hover {
  color: #00cdac;
  text-decoration: none;
}
.cta-btn--resume:hover::after {
  width: 100%;
}

/* Arrow Button */
.up i {
  color: #272727;
}

.footer {
  background-color: #333333;
  text-align: center;
  color: #fff;
  padding: 4.8rem 0;
}
@media (max-width: 37.5em) {
  .footer {
    border: 0px;
  }
}
.footer__text {
  text-align: center;
  color: gray;
  font-size: 1.3rem;
}
.footer__text a {
  color: gray;
  font-size: 1.3rem;
  transition: all 0.4s;
  display: inline-block;
  background-color: #333333;
}
.footer__text a:hover, .footer__text a:active {
  color: #02aab0;
  -webkit-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
}
.footer hr {
  margin: 1rem auto;
  border: 0;
  width: 50%;
  border-top: 2px solid grey;
}

.social-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.social-links a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  font-size: 3rem;
  width: 5rem;
  height: 5rem;
  margin: 1.6rem 1.6rem;
  transition: all 0.2s ease-in;
}
.social-links a:hover {
  transform: translateY(-2px);
}

.back-to-top i {
  color: #fff;
  margin: 1rem 0 1.6rem;
  transition: all 0.2s ease-in;
}
.back-to-top i:hover {
  transform: translateY(-2px);
}

section {
  padding: 5rem 0rem;
}
@media (max-width: 37.5em) {
  section {
    border: none;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

#about {
  background-color: #02aab0;
  background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
  text-align: center;
  color: #fff;
  height: 100%;
  border-top: 0px;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
  padding-bottom: 10%;
}
@media (max-width: 75em) {
  #about {
    height: 100%;
    -webkit-clip-path: none;
    clip-path: none;
  }
}
@media (max-width: 37.5em) {
  #about .about-wrapper {
    padding-bottom: 5rem;
  }
}
#about .about-wrapper__image {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
@media (max-width: 75em) {
  #about .about-wrapper__image {
    height: 100%;
  }
}
@media (max-width: 48em) {
  #about .about-wrapper__image {
    padding-bottom: 6.4rem;
  }
}
#about .about-wrapper__info {
  display: flex;
  height: 100%;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 48em) {
  #about .about-wrapper__info {
    align-items: center;
  }
}
#about .about-wrapper__info-text {
  text-align: left;
}
@media (max-width: 56.25em) {
  #about .about-wrapper__info-text {
    text-align: left;
  }
}
@media (max-width: 48em) {
  #about .about-wrapper__info-text {
    text-align: center;
  }
}
#about .about-wrapper__info-text--important {
  background: #00c8a8;
  display: inline-block;
  font-style: italic;
  padding: 0 0.3rem;
  margin: 0.3rem 0;
  line-height: 1.6;
}
@media (max-width: 37.5em) {
  #about .about-wrapper__info-text--important {
    display: inline;
    margin: 0;
    padding: 0;
    background: transparent;
    font-style: normal;
  }
}

#contact {
  background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
  -webkit-clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%);
  clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%);
  padding: 15rem 0 10rem 0;
  margin-top: -10rem;
  margin-bottom: -1px;
  text-align: center;
  color: #fff;
}
@media (max-width: 75em) {
  #contact {
    padding: 10rem 0;
    clip-path: none;
    margin-top: 0;
    -webkit-clip-path: none;
  }
}
#contact .contact-wrapper {
  margin-top: 3.2rem;
  padding: 0 2rem;
  backface-visibility: hidden;
}
#contact .contact-wrapper__text {
  margin-bottom: 2.5rem;
}
#contact .contact-wrapper__text,
#contact .contact-wrapper a {
  font-size: 2.4rem;
}
@media (max-width: 37.5em) {
  #contact .contact-wrapper__text,
  #contact .contact-wrapper a {
    font-size: 2rem;
  }
}

#hero {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  border-bottom: 0px;
  background: #fff;
  font-weight: 400;
  color: #272341;
  padding: 0rem 5.6rem;
  margin-bottom: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  /* background: url("/src/assets/[your-image].png");
  background-position: center;
  background-size: cover; */
}
@media (max-width: 37.5em) {
  #hero {
    padding: 0rem 1.6rem;
  }
}
#hero .hero-title {
  font-size: 5.6rem;
  font-weight: 700;
  margin-bottom: 3.2rem;
  text-align: left;
}
@media (max-width: 75em) {
  #hero .hero-title {
    font-size: 4rem;
  }
}
@media (max-width: 56.25em) {
  #hero .hero-title {
    font-size: 3.6rem;
    text-align: center;
  }
}
@media (max-width: 37.5em) {
  #hero .hero-title {
    font-size: 3.5rem;
    line-height: 1.5;
  }
}
@media (max-width: 20em) {
  #hero .hero-title {
    font-size: 2.8rem;
  }
}
#hero .hero-cta {
  display: flex;
}
@media (max-width: 56.25em) {
  #hero .hero-cta {
    justify-content: center;
  }
}
#hero .hero-cta a {
  font-size: 2.4rem;
}
@media (max-width: 37.5em) {
  #hero .hero-cta a {
    font-size: 2rem;
  }
}

#projects {
  background-color: #fff;
  text-align: center;
  color: #272341;
  margin-top: -10rem;
  padding-top: 15rem;
}
@media (max-width: 75em) {
  #projects {
    margin-top: 0;
    padding-top: 5rem;
  }
}
#projects .project-wrapper {
  margin-bottom: 15rem;
}
@media (max-width: 37.5em) {
  #projects .project-wrapper {
    margin-bottom: 0rem;
  }
}
#projects .project-wrapper .row {
  margin-bottom: 8rem;
}
@media (max-width: 37.5em) {
  #projects .project-wrapper .row {
    margin-bottom: 4rem;
  }
}
#projects .project-wrapper__text {
  text-align: left;
}
@media (max-width: 37.5em) {
  #projects .project-wrapper__text {
    margin-bottom: 2.5rem !important;
  }
}
@media (max-width: 75em) {
  #projects .project-wrapper__text {
    margin-bottom: 4.8rem;
  }
}
#projects .project-wrapper__text-title {
  font-weight: bold;
  margin-bottom: 1.8rem;
  font-size: 2.5rem;
}
@media (max-width: 37.5em) {
  #projects .project-wrapper__text-title {
    font-size: 2rem;
  }
}
#projects .project-wrapper__text p > a {
  color: #00cdac;
}
#projects .project-wrapper__image {
  width: 90%;
  margin: 0 auto;
}
@media (max-width: 75em) {
  #projects .project-wrapper__image {
    width: 100%;
    margin: 0;
  }
}
#projects .project-wrapper__image .thumbnail {
  border: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: all 0.2s ease-out;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.08), 0 0 6px rgba(0, 0, 0, 0.05);
  transition: 0.5s transform cubic-bezier(0.155, 1.105, 0.295, 1.12), 0.5s box-shadow, 0.5s -webkit-transform cubic-bezier(0.155, 1.105, 0.295, 1.12);
}
@media (max-width: 37.5em) {
  #projects .project-wrapper__image .thumbnail {
    border: 1px solid #d2d2d2;
    box-shadow: none;
    margin-bottom: 3.2rem;
  }
}